import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { AddCategoryService } from '../../services/AddCategoryService';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import swal from 'sweetalert2';

@Component({
  selector: 'list-category',
  templateUrl: './list-category.component.html',
  styleUrls: [
    './list-category.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [AddCategoryService, GetPopularChemistService, GetChemistDataService]
})
export class ListCategoryComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;

  rows = [];
  expanded = {};
  timeout: any;

  rowsFilter = [];
  tempFilter = [];

  rowsFilter2 = [];
  tempFilter2 = [];
  public chemists_array = [];
  public chemist_id: number = 0;
  public chemists_categories = [];
  public chemists_sub_categories = [];
  public category_name: string = "";
  public type: string = "";
  public updateformname: any;
  @ViewChild(DatatableComponent) table: DatatableComponent;
  @ViewChild(DatatableComponent) table2: DatatableComponent;
  public showAttr: boolean = false;
  constructor(public _addCategoryService: AddCategoryService,
    public _getChemistDataService: GetChemistDataService,
    public _getPopularChemistService: GetPopularChemistService) {
    var session_obj = JSON.parse(localStorage.getItem('session_obj'));
    if (session_obj.UserRole == 1) {
      this.getChemists();
      this.showAttr = true;
    }
    else if (session_obj.UserRole == 2) {
      this.chemist_id = session_obj.ChemistId;
      this.showAttr = false;
      this.getCategoriesOfChemists();
    }
  }

  ngOnInit() {
  }

  viewSubCategories(Id) {
    var category = this.chemists_categories.find(x => x.Id == Id);
    this.category_name = category.CategoryName;
    this._addCategoryService.getAllSubCategoryValues(this.chemist_id, Id).subscribe(response => {
      if (response.code == 200) {
        console.log(response.data);
        this.chemists_sub_categories = response.data;
        this.tempFilter2 = this.chemists_sub_categories;
        this.rowsFilter2 = this.chemists_sub_categories;
        $('#sub-cateories-list').show();
      }
    });
  }// End of Get SubCategories

  getCategoriesOfChemists() {
    $('#categories-fetcher').show();
    $('#sub-cateories-list').hide();
    this._addCategoryService.getCateogry(this.chemist_id).subscribe(
      response => {
        $('#categories-fetcher').hide();
        console.log(response.data);
        $('#list').show();
        this.chemists_categories = response.data;
        this.tempFilter = this.chemists_categories;
        this.rowsFilter = this.chemists_categories;
      });
  }// End of Get Categories Of Chemists

  getChemists() {
    this._getChemistDataService.getRegisteredChemistService().subscribe(
      response => {
        // this.chemists_array = response.data;
        
        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i].Chemist);
        }
      });
  }// End of Get Chemists
  public category_update_id: number = 0;
  public sub_category_update_id: number = 0;
  openUpdateCategory(Id) {
    this.type = "Category";
    var category_obj = this.chemists_categories.find(x => x.Id == Id);
    this.updateformname = category_obj.CategoryName;
    this.category_update_id = Id;
  }// Update Category End

  openUpdateSubCategory(Id) {
    this.type = "Sub Category";
    var category_obj = this.chemists_sub_categories.find(x => x.Id == Id);
    this.updateformname = category_obj.CategoryName;
    this.sub_category_update_id = Id;
  }// Update Sub Category End

  update() {
    $('.text-danger').hide();
    if (this.updateformname === undefined || this.updateformname == "" || this.updateformname == null) {
      $('#error-message').show();
      return;
    }
    if (this.type == "Category") {
      var index_number = this.chemists_categories.findIndex(x => x.Id == this.category_update_id);
      this.chemists_categories[index_number].CategoryName = this.updateformname;
      this._addCategoryService.updateCategory(this.category_update_id, this.updateformname).subscribe(response => { });
      this.rowsFilter = this.chemists_categories;
      this.tempFilter = this.chemists_categories;
      $('#close-btn').click();
    }
    else {
      //Sub Category
      var index_number = this.chemists_sub_categories.findIndex(x => x.Id == this.sub_category_update_id);
      this.chemists_sub_categories[index_number].CategoryName = this.updateformname;
      this._addCategoryService.updateCategory(this.sub_category_update_id, this.updateformname).subscribe(response => { 
        console.log(response);
      });
      this.rowsFilter2 = this.chemists_sub_categories;
      this.tempFilter2 = this.chemists_sub_categories;
      $('#close-btn').click();
    }
  }// End of Update

  updateFilter2(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter2.filter(function (d) {
      return d.CategoryName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter2 = temp;
    // Whenever the filter changes, always go back to the first page
    this.table2.offset = 0;
  }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.CategoryName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }
  //Delete Services
  deleteCategoryServices(type, i) {
    // this.measurement.splice(i, 1);
    console.log(i);
    var self = this;
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(function (result) {
      if (result.value == true) {
        if(type == 1){
          //Category Delete
          self._addCategoryService.deleteCategory(i).subscribe(response => {
            var index_number = self.chemists_categories.findIndex(x => x.Id == i);
            self.chemists_categories.splice(index_number, 1);
            self.rowsFilter = self.chemists_categories;
            self.tempFilter = self.chemists_categories;
            swal(
              'Deleted!',
              'Category has been deleted.',
              'success'
            );
          });
        }
        else{
          //Sub Category Delete
          self._addCategoryService.deleteSubCategory(i).subscribe(response => {
            var index_number = self.chemists_sub_categories.findIndex(x => x.Id == i);
            self.chemists_sub_categories.splice(index_number, 1);
            self.rowsFilter2 = self.chemists_sub_categories;
            self.tempFilter2 = self.chemists_sub_categories;
            swal(
              'Deleted!',
              ' Sub Category has been deleted.',
              'success'
            );
          });
        }
      }
      else {
        // alert('No deletion');
      }
    }).catch(swal.noop);
  }
  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }

  getRowHeight(row) {
    return row.height;
  }

}



